<template>
  <div>
    <div class="widgetContainer widgetContainer--center widgetContainer--scrollable bg-white ownersList">
      <div class="navigationHeader">
        <div class="navigationHeader__inner">
          <span
            class="icon-Arrow-big-left cancel"
            @click="goBack" />
          <p class="title">
            {{ $t('ownerList_NavTitle') }}
          </p>
        </div>
      </div>

      <div class="widgetContainer__body">
        <el-form>
          <div class="wise-form">
            <p class="wise-form__header">
              {{ $t('ownerList_HeaderTitle') }}
            </p>
            <div
              class="ownersList__greater-than"
              v-if="owners && owners.length">
              <ul>
                <li v-for="o in owners" :key="o.id">
                  <div class="details">
                    <p class="label">
                      {{ o.person.firstName }} {{ o.person.lastName }}
                    </p>
                    <span class="value">{{ isEditable ? o.ownership : '100' }}%</span>
                  </div>
                  <div>
                    <el-form-item
                      prop="isControlPerson"
                      class="is-no-asterisk column-input">
                      <el-checkbox :disabled="!isEditable || owners.length === 1" @change="controlPersonChange(o)" v-model="o.isControlPerson">
                        {{ $t('ownerList_controlPersonLabel') }}
                      </el-checkbox>
                    </el-form-item>

                    <el-form-item
                      prop="designation"
                      class="column-input"
                      :label="$t('ownerList_designation')">
                      <el-select
                        filterable
                        v-if="o.isControlPerson"
                        v-model="o.designation"
                        class="is-asterisk">
                        <el-option v-for="designation in controlPersonDesignations" :key="designation.value" :label="designation.label" :value="designation.value" />
                      </el-select>

                      <el-select
                        v-else
                        v-model="o.designation"
                        filterable
                        class="is-asterisk">
                        <el-option v-for="designation in designations" :key="designation.value" :label="designation.label" :value="designation.value" />
                      </el-select>
                    </el-form-item>

                    <el-form-item class="is-no-asterisk column-input" :label="$t('ownerList_designationTitle')" :rules="{required: true}" v-if="o.designation === 'other'">
                      <el-input v-model="o.title" />
                    </el-form-item>
                  </div>
                </li>
              </ul>
            </div>

            <div v-if="totalOwnerShip <= 75 && isEditable">
              <p class="wise-form__header">
                {{ $t('ownerList_AddNew_HeaderTitle') }}
              </p>
              <div class="listSimpleArrows no-status" @click="addOwner">
                <li>
                  <p>{{ $t('ownerList_AddNew_SubTitle') }}</p>
                  <span class="icon-Arrow-small-right" />
                </li>
              </div>
            </div>

            <p class="ownersList__verify-description wise-form__info">
              {{ $t('ownerList_Desc') }}
            </p>
          </div>
          <div class="widgetContainer__footer--fixed">
            <el-button :disabled="!isValid" type="primary" @click="proceedTosign" class="el-button__brand brand">
              {{ $t('ownerList_NextButton') }}
            </el-button>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import { businessOwnerDesignations } from '@m/business/utills/business';
import { mapActions, mapMutations, mapGetters } from 'vuex';
import businessMixin from '../../mixins/business';
export default {
  name: 'OwnersList',
  mixins: [businessMixin],
  data() {
    return {
      owners: [],
    };
  },
  computed: {
    ...mapGetters('account', ['getSelectedAccountType']),
    totalOwnerShip() {
      let total = 0;
      this.owners.forEach((o) => {
        total += parseInt(o.ownership);
      });
      return total;
    },
    designations() {
      return  businessOwnerDesignations.filter((d) => !d.isControlPerson);
    },
    controlPersonDesignations() {
      return businessOwnerDesignations.filter((d) => d.isControlPerson)
    },
    isEditable() {
      return !['soleProprietor', 'singleMemberLLC'].includes(this.getSelectedBusiness.entityType || '')
    },
    isValid() {
      let isControlPersonSelected = false;
      let designationCheck = true;
      this.owners.forEach((o) => {
        if(o.isControlPerson) {
          isControlPersonSelected = true
        }
        if(o.designation === '') {
          designationCheck = false
        }
      })
      return isControlPersonSelected && designationCheck
    }
  },
  methods: {
    ...mapActions('kyb', ['fetchAllOwners', 'updateOwner', 'submitKyb']),
    ...mapMutations('kyb', ['setCreatePageOwnerDetails']),
    afterBusiness() {
      this.getAllOwners(this.getSelectedBusiness.id);
    },
    getAllOwners(businessId) {
      const loader = this.showLoader();
      this.fetchAllOwners(businessId)
        .then((res) => {
          if (res.data.length !== 0) {
            let createdPerson = null;
            let controlPerson = null;
            res.data.forEach((o) => {
              if(o.person.id === this.getSelectedBusiness.createdPersonId) {
                createdPerson = o;
              }
              if(o.isControlPerson) {
                controlPerson = o;
              }
            })
            if(!controlPerson && createdPerson) {
              createdPerson.isControlPerson = true;
            }
            /** @type {import('@m/kyb/store/state').kybState['owners']}*/
            this.owners = res.data;
          }
        })
        .catch((e) => {
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          loader.close();
        });
    },
    addOwner() {
      const loader = this.showLoader();
      this.patchOwners().then(() => {
        this.setCreatePageOwnerDetails(null);
        this.$router.replace('/kyb/ownerDetails');
      }).finally(() => {
        loader.close();
      });
    },
    proceedTosign() {
      if(this.isValid) {
       
        const loader = this.showLoader();
        this.patchOwners()
          .then(() => {
            setTimeout(() => {
              if (!['soleProprietor'].includes(this.getSelectedBusiness.entityType)) {
                this.$router.push('/kyb/eSign')
              } else {
                const loader2 = this.showLoader();
                this.submitKyb(this.getSelectedBusiness.id)
                  .then(() => {
                    this.$router.push('/kyb/businessStatus');
                  }) .catch((e) => {
                    console.log(e);
                    this.apiErrorMessage(e);
                  }).finally(() => {
                    loader2.close();
                  });
              }
            }, 0)
          })
          .finally(() => {
            loader.close();
          });
      }
    },
    patchOwners() {
      const promises = [];

      this.owners.forEach((o) => {
        const { designation } = o;
        const ownerData = {
          isControlPerson: o.isControlPerson,
        };

        if (designation !== '') {
          ownerData.designation = designation;
          if (designation === 'other') {
            ownerData.title = o.title;
          }

          if (!this.isEditable) {
            ownerData.ownership = '100';
          }
          promises.push(this.updateOwner({ownerId: o.id, details: ownerData}))
        }
      });
      return Promise.all(promises);
    },
    controlPersonChange(owner) {
      owner.designation = '';
      this.owners.forEach((o) => {
        if(o.id !== owner.id && o.isControlPerson) {
          o.isControlPerson = false;
          o.designation = '';
        }
      })
    },
    goBack() {
      if(this.isEditable) {
        this.$router.replace('/kyb/ownership')
      } else {
        this.$router.replace('/kyb/businessAddress')
      }
    }
  }
};
</script>

