export const EtvPlainToOption = (plainObj) => {
  const fieldOptionsMap = getETVFieldOptionsMap();
  Object.keys(plainObj).forEach((key) => {
    Object.keys(plainObj[key]).forEach((k) => {
      // find by max count
      const { count, amount } = plainObj[key][k];
      if (amount !== undefined && count !== undefined) {
        const selectedCount = parseFloat(count);
        const selectedAmount = parseFloat(amount);

        const selectedObj = fieldOptionsMap[k].find((obj) => {
          const maxCountArr = obj.count.split('-');

          if (maxCountArr.length === 2) {
            const start = parseFloat(maxCountArr[0].replaceAll(' ', '').replaceAll('+', '').replaceAll('$', ''));
            const end = parseFloat(maxCountArr[1].replaceAll(' ', '').replaceAll('+', '').replaceAll('$', ''));
            // console.log("start, end", start, end)
            return (!Number.isNaN(start) && !Number.isNaN(end) && !Number.isNaN(selectedCount) && selectedCount >= start && selectedCount <= end);
          }
          const maxCount = maxCountArr[maxCountArr.length - 1].replaceAll(' ', '').replaceAll('+', '').replaceAll('$', '');
          const maxCountNumber = parseFloat(maxCount);
          return (!Number.isNaN(maxCountNumber) && !Number.isNaN(selectedCount) && maxCountNumber <= selectedCount);
        });
        const selectedAmountObj = fieldOptionsMap[k].find((obj) => {
          const maxCountArr = obj.amount.split('-');

          if (maxCountArr.length === 2) {
            const start = parseFloat(maxCountArr[0].replaceAll(',', '').replaceAll(' ', '').replaceAll('+', '').replaceAll('$', ''));
            const end = parseFloat(maxCountArr[1].replaceAll(',', '').replaceAll(' ', '').replaceAll('+', '').replaceAll('$', ''));
            // console.log("start, end", start, end, maxCountArr)
            return (!Number.isNaN(start) && !Number.isNaN(end) && !Number.isNaN(selectedAmount) && selectedAmount >= start && selectedAmount <= end);
          }
          const maxCount = maxCountArr[maxCountArr.length - 1].replaceAll(',', '').replaceAll(' ', '').replaceAll('+', '').replaceAll('$', '');
          const maxCountNumber = parseFloat(maxCount);
          return (!Number.isNaN(maxCountNumber) && !Number.isNaN(selectedAmount) && maxCountNumber <= selectedAmount);
        });
        if (selectedObj) {
        // eslint-disable-next-line no-param-reassign
          plainObj[key][k].count = selectedObj.count;
        }
        if (selectedAmountObj) {
        // eslint-disable-next-line no-param-reassign
          plainObj[key][k].amount = selectedAmountObj.amount;
        }
      }
    });
  });
  
  return plainObj;
};

